.images1 {
  /* height: 40rem; */
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, 18rem);
  grid-template-rows: repeat(auto-fit, 40rem);
  gap: 1rem;
  margin: 10px;
}

img {
  height: 40rem;
}

.button56 {
  align-items: center;
  /* background-color: #fee6e3; */
  background-color: #0afa2e;
  border: 2px solid #111;
  border-radius: 8px;
  box-sizing: border-box;
  color: #111;
  cursor: pointer;
  display: flex;
  font-family: Inter, sans-serif;
  font-size: 16px;
  height: 48px;
  justify-content: center;
  line-height: 24px;
  max-width: 100%;
  padding: 0 25px;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button56:after {
  background-color: #111;
  border-radius: 8px;
  content: '';
  display: block;
  height: 48px;
  left: 0;
  width: 100%;
  position: absolute;
  top: -2px;
  transform: translate(8px, 8px);
  transition: transform 0.2s ease-out;
  z-index: -1;
}

.button56:hover:after {
  transform: translate(0, 0);
}

.button56:active {
  /* background-color: #ffdeda; */
  background-color: #0afa2e;
  outline: 0;
}

.button56:hover {
  outline: 0;
}

@media (min-width: 768px) {
  .button56 {
    padding: 0 40px;
  }
}

.download {
  margin: 20px;
  display: flex;
  justify-content: center;
}

.download > a {
  text-decoration: none;
}
